import { createBrowserRouter } from "react-router-dom";
import App from "../App";
import AboutUs from "../pages/AboutUs";
import Atlye from "../pages/Atlye";
import Oyun from "../pages/Oyun";

const router = createBrowserRouter([
    {
        path: "/",
        element: <App />,
    },
    {
        path: "/oyun",
        element: <Oyun />,
    },
    {
        path: "/hakkmzda",
        element: <AboutUs />,
    },
    {
        path: "/atlye",
        element: <Atlye />,
    },
]);

export default router;